  display: block;
}

/*
 * Add the correct display in all browsers.
 */
summary {
  display: list-item;
}

/* Misc
   ========================================================================== */
/**
 * Add the correct display in IE 10+.
 */
template {
  display: none;
}

/**
 * Add the correct display in IE 10.
 */
[hidden] {
  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  background-color: #1a1b15;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  color: #0da6f3;
  font-family: Poppins, sans-serif;
  font-weight: 900;
  margin: 0 0 2.5rem;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

h4 {
  font-size: 2.6rem;
}

label {
  font-size: 1.8rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.centrar-p {
  text-align: center;
}

/*UI*/
.formulario {
  width: 100%;
  margin-top: 5rem;
}

.campo {
  display: flex;
  margin-bottom: 5rem;
  align-items: center;
}
.campo label {
  flex: 0 0 13rem;
  color: #FFFFFF;
}
.campo input, .campo select {
  flex: 1;
  border: none;
  padding: 1.6666666667rem;
  border-radius: 1rem;
}

.seccion-login {
  margin: 2rem 0;
}
@media (min-width: 1024px) {
  .seccion-login {
    margin: 5rem 0;
  }
}

.boton {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  border: none;
  cursor: pointer;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  font-weight: 700;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #0a85c3;
}

.vacaciones {
  border-bottom: 1px solid #fff;
}
.vacaciones .btn-vac {
  margin-bottom: 2rem !important;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 1024px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  color: #FFFFFF;
  display: block;
  margin-bottom: 2.5rem;
  text-align: center;
  font-size: 1.8rem;
}

.accionesSin {
  margin-top: 5rem;
}
.accionesSin a {
  text-align: center;
  margin-bottom: 2.5rem;
  color: #FFFFFF;
  font-size: 3rem;
  transition-property: transform;
  transition-duration: 300ms;
}
.accionesSin a:hover {
  color: #0da6f3;
  transform: scale(1.1);
}

nav {
  position: absolute;
  background-color: #0da6f3;
  z-index: 1;
  width: 100%;
  max-height: 100%;
  opacity: 0;
  overflow: hidden;
  transition: max-height 1s ease, opacity 0.5s ease;
}
nav a {
  display: block;
  text-decoration: none;
  font-size: 1.8rem;
  padding: 1rem 0 1rem 1rem;
  color: #FFFFFF;
  font-weight: 700;
  transition: background-color 0.35s ease, color 0.35s ease;
}
nav a:hover {
  background-color: #0a85c3;
}
@media (min-width: 1024px) {
  nav {
    grid-column: 2;
    display: flex;
    visibility: visible;
    opacity: 1;
    max-height: 100%;
    width: 100%;
    z-index: 10;
    margin-bottom: 10rem;
  }
  nav a {
    display: flex;
    height: 5rem;
    width: 100%;
    justify-content: space-evenly;
    transition: background-color 0.35s ease, color 0.35s ease;
  }
}

.mobile-menu {
  display: flex;
  justify-content: flex-end;
  background-color: #0da6f3;
  width: 100%;
  height: 100%;
}
.mobile-menu img {
  width: 3rem;
  margin: 1rem 2rem 1rem 0;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  max-height: 100%;
  transition: max-height 1s ease, opacity 0.5s ease;
}

.active {
  background-color: #FFFFFF !important;
  color: #0da6f3 !important;
}

.icono {
  font-size: 2rem;
  color: #000;
}

.p-alertas {
  color: #000;
}

.btn-cancelar {
  font-size: 2rem !important;
  background-color: red !important;
}

.btn-confirmar {
  font-size: 2rem !important;
  background-color: #0da6f3 !important;
}

p.p-alertas {
  color: #000;
}

.titulo {
  font-size: 2.2rem;
}

.alerta-ancha {
  width: 50%;
}

.swal2-popup .swal2-input {
  max-width: 100%;
  overflow: hidden;
}

.mensaje-error {
  background-color: red;
  padding: 2rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}

.in-alertas {
  font-size: 1.8rem;
}

.paginado {
  display: none;
}

.mostrado {
  display: block;
}

@media (min-width: 768px) {
  .nav {
    display: flex;
    margin: 5rem 0 0 0;
  }
}
.nav button {
  background-color: #0da6f3;
  border: none;
  padding: 1rem;
  color: #FFFFFF;
  display: block;
  margin-bottom: 1rem;
  width: 100%;
  font-size: 2rem;
  font-weight: 700;
  text-transform: uppercase;
  transition: background-color 0.35s ease;
}
.nav button:hover {
  background-color: #0a85c3;
  cursor: pointer;
}

.pagina {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .pagina {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5rem;
  }
}
.pagina .boton {
  margin: 1rem 0;
}

.ocultar {
  visibility: hidden;
}

.listado-servicios {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 2rem;
    place-items: center;
    column-gap: 2rem;
  }
}
.listado-servicios .servicio {
  background-color: #FFFFFF;
  width: 100%;
  padding: 3rem;
  border-radius: 1rem;
  transition-property: transform;
  transition-duration: 300ms;
  text-align: center;
}
.listado-servicios .servicio.seleccionado {
  background-color: #0da6f3;
}
.listado-servicios .servicio.seleccionado p {
  color: #FFFFFF;
}
.listado-servicios .servicio p {
  color: #000;
  margin: 0 0 1.25rem 0;
  font-size: 2.2rem;
}
.listado-servicios .servicio .precio-servicio {
  color: #0da6f3;
  font-weight: 700;
  font-size: 3rem;
  margin: 0;
}
.listado-servicios .servicio:hover {
  transform: scale(1.07);
}

.contendor-servicio {
  border-bottom: 1px solid #FFFFFF;
  padding: 2rem 0;
  margin-bottom: 1rem;
}

.contenido-resumen span {
  color: #0da6f3;
  font-weight: bold;
  font-size: 2.4rem;
}

.h-resumen {
  font-weight: 400;
  color: #FFFFFF;
  margin: 5rem 0 0 0;
  padding: 0;
}

.derecha {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 0;
}
.derecha .reservar-cita {
  margin: 0;
}
.derecha .boton {
  margin: 1rem;
}

.resumen {
  margin-bottom: 2.5rem;
}

.num-citas {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-bottom: 5px solid #FFFFFF;
}
@media (min-width: 768px) {
  .num-citas {
    flex-direction: row;
    justify-content: space-around;
    margin-top: 3rem;
  }
}

.datos-cita {
  text-align: center;
  margin-top: 2rem;
}

.contenedor-cita {
  margin-top: 1rem;
  padding: 0;
}
.contenedor-cita p {
  color: #FFFFFF;
  font-weight: 400;
  font-size: 2rem;
  margin: 0 0 1.8rem 0;
}
.contenedor-cita p span {
  color: #0da6f3;
  font-weight: bold;
  font-size: 2.2rem;
}
.contenedor-cita p .estado {
  color: #FFFFFF;
  font-weight: 400;
  font-size: 2rem;
  text-transform: uppercase;
}
.contenedor-cita .contenido-servicio {
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .contenedor-cita {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 2rem;
  }
  .contenedor-cita .contenido-servicio {
    grid-column: 2;
    border-left: 1px solid #FFFFFF;
    margin-top: 0;
  }
}

.ver-citas {
  border-bottom: 1px solid #FFFFFF;
  margin: 3rem 0;
}
.ver-citas .h-resumen {
  margin: 1rem 0 0;
}

.contenedor-servicio {
  margin-top: 1rem;
  padding: 0;
}
.contenedor-servicio p {
  color: #FFFFFF;
  font-weight: 400;
  font-size: 2rem;
  margin: 0 0 1.8rem 0;
}
.contenedor-servicio p span {
  color: #0da6f3;
  font-weight: bold;
  font-size: 2.2rem;
}
.contenedor-servicio .datos-cita {
  text-align: center;
  margin-top: 1rem;
}

/*Internas*/
.app {
  margin: 0 auto;
  padding-top: 2.5rem;
  width: 95%;
}
@media (min-width: 1024px) {
  .app {
    grid-column: 2;
  }
}

@media (min-width: 1024px) {
  .header {
    margin-top: 5rem;
  }
}

@media (min-width: 1024px) {
  .contenedor-estetica {
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 2rem;
    height: 100vh;
  }
}
.contenedor-estetica .imagen {
  position: relative;
  background-image: url(../img/1.jpg);
  background-position: 50%;
  background-size: cover;
  height: 30rem;
}
@media (min-width: 1024px) {
  .contenedor-estetica .imagen {
    position: fixed;
    top: 0;
    left: 0;
    width: 50%;
    height: 100vh;
  }
}
.contenedor-estetica h2 {
  margin-bottom: 0;
}
.contenedor-estetica h1 {
  margin-top: 1rem;
}

.texto-arriba {
  margin-bottom: 3rem;
}
@media (min-width: 1024px) {
  .texto-arriba {
    margin: 6.5rem 0 0 0;
  }
}